import React, {useState, useEffect} from "react"
import { createUseStyles } from "react-jss";
import {loadableP5 as Sketch} from "../../helpers/p5";

const useStyles = createUseStyles({
  nav: {
    display: 'flex',
    justifyContent: 'space-between',
    height: 20,
    width: '100%',
    backgroundColor: 'white',
    '& p': {
      cursor: 'pointer',
      margin: 12,
      fontSize: 12,
      '&:hover': {
        color: 'red'
      },
    }
  },
  sticky: {
    borderBottom: '1px dashed black',
    position: 'fixed',
    top: 0,
    left: 0,
  },
});

const Nav = () => {
  const [yOffset, setYoffset] = useState(0);
  const [direction, setDirection] = useState(2);
  const [x, setX] = useState(50);
  const classes = useStyles();
  
  const handleWindowResized = p5 => p5.resizeCanvas(p5.windowWidth, 20);
  
  const setup = (p5, canvasParentRef) =>
    p5.createCanvas(p5.windowWidth < 360 ? p5.windowWidth - 30 : 360, 20).parent(canvasParentRef);
  
  const draw = p5 => {
    p5.background(255);
    p5.ellipse(x, 10, 15, 15);
    
    if (x === p5.width - 10) setDirection(-2);
    if (x === 10) setDirection(2);
    
    setX(x + direction)
    
  };
  
  useEffect(() => {
    const handleScroll = () => setYoffset(window.pageYOffset);
    window.addEventListener('scroll', handleScroll);
    
    return () => window.removeEventListener('scroll', handleScroll);
  });
  
  return (
    <div className={[classes.nav, yOffset > 20 && classes.sticky].join(' ')}>
      <Sketch setup={setup} draw={draw} windowResized={handleWindowResized}/>
    </div>
  );
};

export default Nav;